/* The container <div> - needed to position the dropdown content */
.search-dropdown {
  width:70vmin;
  position: relative;
}

input[id=new-location] {
  margin-top:10px;
  -webkit-box-shadow: 2px 8px 16px 2px rgba(0,0,0,0.2);
          box-shadow: 2px 8px 16px 2px rgba(0,0,0,0.2);
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  border: 1px solid transparent;
  background-color: #f1f1f1;
  padding: 5px 5px 5px 5px;
  font-size: 14px;
  background-color: #f1f1f1;
  width: 100%;
  -ms-flex:1 1;
      flex:1 1;
  border-radius: 5px;
}

/* Dropdown Content (Hidden by Default) */
.search-dropdown-content {
  width:100%;
  z-index:100;
  visibility: visible;
  max-height:var(--searchbox-height);
  position: absolute;
  background-color: #f9f9f9;
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
          box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  overflow:hidden;
  -ms-flex:1 1;
      flex:1 1;
  border: solid 0px red;
  -webkit-transition: max-height 1s linear;
  -o-transition: max-height 1s linear;
  transition: max-height 1s linear;
  border-radius: 5px;
}

/* Links inside the dropdown */
.search-dropdown-content ul {
  color: black;
  padding: 3px;
  text-decoration: none;
  display: block;
  cursor: pointer;
  list-style: none;
  text-align:left; 
  margin:0px; 
  padding:0px 0px 0px 0px;
}

.search-dropdown-content-li {
  border: 1px solid transparent;
  background-color: transparent;
  color:black;
  padding: 10px;
  font-size: 14px;
  border-bottom: solid 0.25px lightgray
}
/* Change color of dropdown links on hover */
.search-dropdown-content li:hover {background-color: #f1f1f1}

/* Change color of dropdown links on active */
.li-active {
  background-color: #f1f1f1;
}

/* Links inside the dropdown */
.item-content-ul {
  color: black;
  padding: 3px;
  text-decoration: none;
  display: block;
  cursor: pointer;
  list-style: none;
  text-align:left; 
  margin:0px; 
  padding:0px 0px 0px 0px; 
}

.item-content-li {
  border: 1px solid transparent;
  background-color: transparent;
  color:black;
  padding: 10px;
  font-size: 14px;
  border-bottom: solid 0.25px lightgray;
  -webkit-animation: AddingAnimation 1s forwards 0s ease-in-out;
          animation: AddingAnimation 1s forwards 0s ease-in-out;
}

.inline-search-text {
  text-decoration: underline;
}

.div-list-items {
  padding-top: 40px;
}

.div-list-title {
  font-weight: bold;
  font-size: medium;
  padding: 10px;
  color:transparent;  
}

.list-item-container {
  width:100%;
  text-align: left;
  font-size: 9pt;
  -ms-flex-align: center;
      align-items: center;
  border: solid 0px red;
  display:grid;
  grid-template-columns: 65% auto 15px ;
}

.list-item {
  -webkit-text-decoration: var(--hover-textdecoration);
          text-decoration: var(--hover-textdecoration);
  border: solid 0px blue;
}

.list-item:first-child {
  -webkit-text-decoration: var(--hover-textdecoration);
          text-decoration: var(--hover-textdecoration);
  color:rgba(0, 0, 0, 0.900);
  font-weight:lighter
}

.list-item:nth-child(2) {
  -webkit-text-decoration: var(--hover-textdecoration);
          text-decoration: var(--hover-textdecoration);
  text-align: right;
  font-weight:lighter;
}

.list-item:last-child {
  -webkit-text-decoration: var(--hover-textdecoration);
          text-decoration: var(--hover-textdecoration);
  text-align: right;  
  font-weight: normal;
  font-size: 7pt;
  color:red
}

.list-item:hover {
  --hover-textdecoration:none;
}



