body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, right bottom, from(#ff9966), to(#ff99cc)) fixed;
  background: -webkit-linear-gradient(top left, #ff9966 0%, #ff99cc 100%) fixed;
  background: -o-linear-gradient(top left, #ff9966 0%, #ff99cc 100%) fixed;
  background: linear-gradient(to bottom right, #ff9966 0%, #ff99cc 100%) fixed;
  /* 
  background: linear-gradient(270deg, #04f251, #f86964) ;
  background-size: 400% 400%;
  
  -webkit-animation: AppBackground 20s ease infinite;
  -moz-animation: AppBackground 20s ease infinite;
  -o-animation: AppBackground 20s ease infinite;
  animation: AppBackground 20s ease infinite;
  */
}

@-webkit-keyframes AppBackground {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AppBackground { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}



